<template>
  <el-container>
    <el-main>
      <el-row type="flex" justify="center">
        <el-col :span="10">
          <div class="logo-text">
            短链接服务Demo
          </div>
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <!-- 缩短网址 -->
            <el-tab-pane label="缩短网址" name="first">
              <el-row type="flex" justify="space-between">
                <el-col :span="20">
                  <el-input placeholder="请输入长地址" v-model="url"/>
                </el-col>
                <el-col :span="4">
                  <el-button type="primary" class="btn" @click="getShortUrl">缩短网址</el-button>
                </el-col>
              </el-row>
              <el-row>
                <div v-if="resultUrl!=''" class="alert-info">
                  <el-alert
                    :title="'生成短链如下:'+ this.resultUrl"
                    type="success">
                  </el-alert>
                </div>
              </el-row>
            </el-tab-pane>
            <!-- 缩短网址 -->

            <!-- 还原网址 -->
            <el-tab-pane label="还原网址" name="second">
              <el-row type="flex" justify="space-between">
                <el-col :span="20">
                  <el-input placeholder="请输入短地址" v-model="url"/>
                </el-col>
                <el-col :span="4">
                  <el-button type="primary" class="btn" @click="getLongUrl">还原网址</el-button>
                </el-col>
              </el-row>
              <el-row>
                <div v-if="resultUrl!=''" class="alert-info">
                  <el-alert
                    :title="this.resultUrl"
                    type="success">
                  </el-alert>
                </div>
              </el-row>
            </el-tab-pane>
            <!-- ./还原网址 -->

            <!-- 自定义短链代码 -->
            <el-tab-pane label="自定义短码" name="third">
              <el-row type="flex" justify="space-between">
                <el-col :span="12">
                  <el-input placeholder="请输入长地址" v-model="url"/>
                </el-col>
                <el-col :span="4">
                  <el-button type="primary" class="btn" @click="bindShortCode">绑定短码</el-button>
                </el-col>
                <el-col :span="8">
                  <el-input placeholder="请输入短码" v-model="shortCode"/>
                </el-col>
              </el-row>
              <el-row>
                <div v-if="resultUrl!=''" class="alert-info">
                  <el-alert
                    :title="this.resultUrl"
                    type="success">
                  </el-alert>
                </div>
              </el-row>
            </el-tab-pane>
            <!-- ./自定义短链代码 -->

            <!-- 数据统计 -->
            <el-tab-pane label="数据统计" name="fourth">
              <el-row type="flex" justify="space-between">
                <el-table
                  :data="tableData"
                  stripe
                  style="width: 100%">
                  <el-table-column
                    prop="longUrl"
                    label="长链接"
                    width="180">
                  </el-table-column>
                  <el-table-column
                    prop="shortUrl"
                    label="短链接"
                    width="180">
                  </el-table-column>
                  <el-table-column
                    prop="visitSize"
                    label="访问量"
                    width="80">
                  </el-table-column>
                  <el-table-column
                    prop="createTime"
                    label="创建时间">
                  </el-table-column>
                </el-table>
              </el-row>
              <el-row>
                <!-- 分页 -->
                <el-pagination
                  class="pagination"
                  background=""
                  @current-change="handleCurrentChange"
                  :page-size="10"
                  layout="total, prev, pager, next"
                  :total="total">
                </el-pagination>
                <!-- 分页 -->
              </el-row>
            </el-tab-pane>
            <!-- ./数据统计 -->

          </el-tabs>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>

export default {
  name: 'HomePage',
  components: {},
  data () {
    return {
      activeName: 'first',
      url: '',
      shortCode: '',
      message: this.$route.params.message,
      resultUrl: '',
      tableData: '',
      total: ''
    }
  },
  methods: {
    handleClick (tab) {
      // 每次切换tab置url和短码为空
      this.url = ''
      this.shortCode = ''
      this.resultUrl = ''
      console.log(tab.name)
      if (tab.label === '数据统计') {
        this.axios.get('http://localhost/apis/list')
          .then(response => {
            this.tableData = response.data.rows
            this.total = response.data.total
          })
      }
    },
    getShortUrl () {
      this.axios.get('http://localhost/apis/getShortUrl?longUrl=' + this.url)
        .then(response => {
          if (response.data.status == '200') {
            // 成功
            this.resultUrl = response.data.data
          } else this.$message.error(response.data.msg)
        })
    },
    getLongUrl () {
      this.axios.get('http://localhost/apis/getLongUrl?shortUrl=' + this.url)
        .then(response => {
          if (response.data.status == '200') {
            // 还原成功
            this.message = response.data.msg
            this.resultUrl = response.data.data
          } else {
            this.$message.error(response.data.msg)
          }
        })
    },
    bindShortCode () {
      // 检查两个是否为空
      if (this.shortCode === '' || this.url === '') {
        this.$message.error('请检查您的输入是否正确!')
        return
      }
      // 检查shortCode是否满足要求
      if (this.shortCode.length !== 6) {
        this.$message.error('短链长度要求为6位,请您检查后再输入!')
        return
      }
      this.axios.get('http://localhost/apis/saveShortUrl?longUrl=' + this.url + '&shortCode=' + this.shortCode)
        .then(response => {
          if (response.data.status == '200') {
            // 绑定成功
            this.$message.success(response.data.msg)
            this.resultUrl = 'http://127.0.0.1/r/' + this.shortCode
          } else {
            this.$message.error(response.data.msg)
          }
        })
    },
    handleCurrentChange (val) {
      this.axios.get('http://localhost/apis/list' + '?pageNum=' + val + '&pageSize=10')
        .then(response => {
          this.tableData = response.data.rows
          this.total = response.data.total
        })
    }
  }

}
</script>

<style scoped>

  .logo-text {
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    margin-top: 120px;
    margin-bottom: 20px;
  }

  .btn {
    width: 100%;
  }

  .alert-info {
    margin-top: 20px;
  }

  .pagination {
    float: right;
  }
</style>



